.App {
	display: flex;
	height: 100vh;
	justify-content: center;
	align-items: center;
	&__container {
		width: 900px;
		height: 350px;
		background-color: rgb(48, 47, 47);
		display: grid;
		grid-template-columns: 250px 650px;
		&_light {
			width: 900px;
			height: 350px;
			background-color: #fff;
			display: grid;
			grid-template-columns: 250px 650px;
		}
	}
}
